import React, {useEffect, useState, useRef} from "react";

// prism //
import Highlight, { defaultProps } from "prism-react-renderer";
import light from "prism-react-renderer/themes/vsDark";
import dark from "prism-react-renderer/themes/vsLight";

//
import { Nav } from "./home";
import "./styles/code.css";
import axios from "axios";
import { ReactComponent as True } from "./images/true.svg";
import { ReactComponent as Close } from "./images/close.svg";
import { ReactComponent as Run } from "./images/run.svg";
import { ReactComponent as Sun } from "./images/sun.svg";
import moon from "./images/moon.png";
import { ReactComponent as FullScreen } from "./images/full-screen.svg";
import { ReactComponent as NotFullScreen } from "./images/small-screen.svg";
import { ReactComponent as Flag } from "./images/flag.svg";
import { Loader } from "./loader";

const Test = ({ num, input, result }) => {
    return <>
        <div className="w-100 test">
            <h5 className="g w-100">تجربة {num}</h5>
            <code>
                <pre className="p-2 fs-6 g">
                    {`${input}   =>   ${result}`}
                </pre>
            </code>
        </div>
    </>
}

const Success = ({ next }) => {
  const fullScreen = useRef(null);
  let encourageWords = ["عاش يا وحش", "فاااجر", "تنين !", "إبن مصر",
    "و إذا كانت النفوس كبارا تعبت في مرادها الأجسام",
    "- قال رسولُ اللهِ صلَّى اللهُ عليه وسلَّم لرجلٍ وهو يَعِظُه : اغتنِمْ خمسًا قبل خمسٍ : شبابَك قبل هَرَمِك، وصِحَّتَك قبل سَقَمِك، وغناك قبل فقرِك، وفراغَك قبل شُغلِك، وحياتَك قبل موتِك.",
    "نعمتان مغبون فيهما كثير من الناس: الصحة، والفراغ"];
  const nextChallenge = (id) => {
    window.location.pathname = `challenge/${id}`;
  }

  useEffect(() => {
    let id = window.location.pathname.replace("/challenge/", "");
    fullScreen.current.style.height = `${document.body.offsetHeight}px`;
    axios.put(`/challenges/passed/${id}`, {});
  }, []);

  return <>
    <div className="success d-flex justify-content-center align-items-center" ref={fullScreen}>
      <div className="d-flex justify-content-center align-items-center flex-wrap p-3">
      <span className="d-flex justify-content-center align-items-center">
        <True/>
        </span>
        <p className="text-white fs-5 text-center w-100 p-3 my-2">{encourageWords[Math.floor(Math.random() * encourageWords.length)]}</p>
        <div className="w-100 my-2 d-flex justify-content-center align-items-center flex-wrap">
      {next !== undefined ? 
        <button className="signin w-75 py-1 fw-bold my-2" onClick={() => nextChallenge(next)}>التحدي التالي</button>
            : <p className="w-100 text-white fs-5 text-center my-2">لقد أنهيت جميع التحديات</p>}
          <a href="/challenge" className="text-decoration-none w-75 py-1 btn-2 text-center">قائمة التحديات</a>
          </div>
      </div>
    </div>
  </>
}

const P = ({ str }) => {
  const p = useRef(null);

  useEffect(() => {
    let text = str.replace(/{{(\w+)}}/gi, "<mark class='px-1'>$1</mark>")
    p.current.innerHTML = text;
  });

  return <>
    <p ref={p} className="text-white w-100"></p>
  </>
}

const Code = () => {
    const [data, setData] = useState([null]);
    const [fullS, setFullS] = useState(false);
    const [darkT, setDarkT] = useState(true);
    const [fontSize, setFontSize] = useState("16");
    const [code, setCode] = useState(`function func() {
   // write your function here
} `);
    const [result, setResult] = useState([]);
    const parent = useRef(null);
    useEffect(() => {
        let id = window.location.pathname.replace("/challenge/", "");
        fetch(`/challenges/${id}`)
            .then(res => res.json())
            .then(data => {
                setData(data);
            });
        let elem = document.getElementById("high-light");
        document.getElementById("textarea").style.width = `calc(${window.getComputedStyle(elem).getPropertyValue("width")} - 25.5px)`;
         document.getElementById("textarea").style.height = `${window.getComputedStyle(elem).getPropertyValue("height")}`;

    }, []);
  
  useEffect(() => {
      window.onresize = () => {
        let elem = document.getElementById("high-light");
        document.getElementById("textarea").style.width = `calc(${window.getComputedStyle(elem).getPropertyValue("width")} - 30px)`;
        document.getElementById("textarea").style.height = `${window.getComputedStyle(elem).getPropertyValue("height")}`;
      }
  });

    const test = () => {
        let id = window.location.pathname.replace("/challenge/", "");
        axios.post(`/test/${id}`, {code: code})
            .then(res => setResult(res.data))
        .catch(err => console.log(err));
    }
  
    function screen () {
      document.getElementById("editor").classList.toggle("full-screen");
      let elem = document.getElementById("high-light");
      document.getElementById("textarea").style.width = `calc(${window.getComputedStyle(elem).getPropertyValue("width")} - 30px)`;
      document.getElementById("textarea").style.height = `${window.getComputedStyle(elem).getPropertyValue("height")}`;
      document.getElementById("editor").style.height = `${window.innerHeight -61}px`;
      document.body.style.height = `${window.innerHeight}px`;
      setFullS(!fullS);
  }
  
  function scroll() {
    let y = document.getElementById("textarea").scrollTop;
    let x = document.getElementById("textarea").scrollLeft;
    document.getElementById("high-light").scrollTo(x, y);
  }

  const hardness = (str) => {
    switch (str) {
      case "easy":
        return 10;
      case "normal":
        return 15;
      case "hard":
        return 20;
      case "very-hard":
        return 25;
      default:
        return "";
    }
  }

  const translate = (str) => {
        switch (str) {
      case "easy":
        return "سهل";
      case "normal":
        return "متوسط";
      case "hard":
        return "صعب";
      case "very-hard":
        return "صعب جدا";
      default:
        return "";
    }
  }

    return <>
      <Nav />
      {data.length > 0 ? 
        <Loader />
        : null
      }
      

        <section className="top container-fulid parent overflow-hidden" ref={parent}>
          <div className="row overflow-hidden">
            <section className="col-lg-6 col-12 p-4 overflow-hidden pb-5">
              <div className="d-flex justify-content-between align-items-center">
                <p className="m-0 points">لقد إجتاز هذا التحدي : {data.peoplePassed}</p>
                <span className="points">
                  {hardness(data.type)}     {"نقطة"}       <Flag />
                </span>
              </div>
              <span className={`type-${data.type} px-2 py-1 fw-bold my-2 d-inline-block`}>{translate(data.type)}</span>
              <h2 className="text-white">{data.name}</h2>
              <P str={data.description ? data.description : ""} />
              {data.testing ?
                JSON.parse(data.testing).map((obj, index) => {
                  return <Test num={index + 1} key={index} input={obj.input} result={obj.result} />
                })
                : null
              } 
              </section>
            <section className="col-lg-6 col-12 d-flex flex-wrap overflow-hidden align-content-start" id="editor">
              <div className="w-100 ltr py-2 px-3 gray d-flex justify-content-between">
                <div className="d-flex justify-content-between align-items-center px-3">
                  <button className="signin py-1 px-2 lose mx-2" onClick={() => setCode(data.solution)}>أستسلم أرني الكود</button>
                  <button className="signin run py-1 px-2 fw-bold d-flex justify-content-center align-items-center" onClick={() => test()}>
                    <Run />
                  </button>
                </div>
                <div className="d-flex align-items-center">
                  <button className="p-1 mx-2" onClick={() => screen()}>
                    {!fullS ? <FullScreen /> : <NotFullScreen />}
                  </button>
                  <button className="p-1 mx-2 run" onClick={() => setDarkT(!darkT)}>
                    {darkT ? <Sun /> : <img src={moon} alt="dark mode" />}
                  </button>
                  <select className="ltr" onChange={(e) => setFontSize(e.target.value)} value={fontSize}>
                    <option value="14" className="ltr">14 px</option>
                    <option value="16" className="ltr">16 px</option>
                    <option value="18" className="ltr">18 px</option>
                    <option value="20" className="ltr">20 px</option>
                    <option value="22" className="ltr">22 px</option>
                    <option value="24" className="ltr">24 px</option>
                  </select>
                </div>
              </div>
              <div className="w-100 cont d-flex overflow-hidden">
                <textarea
                  wrap="off"
                  value={code}
                  spellCheck="false"
                  onChange={(e) => setCode(e.target.value)}
                  className={`write m-0 ltr text-start overflow-hidden ${!darkT ? "light" : null} fs-${fontSize}`}
                  id="textarea"
                  onScroll={() => scroll()}
                >
                </textarea>
                <Highlight {...defaultProps} code={code} language="javascript"
                  className={`textarea ltr ff-code fs-${fontSize}`}
                  theme={!darkT ? dark : light}>
                  {({ className, style, tokens, getLineProps, getTokenProps }) => {
                    return (
                      <pre className={`${className} textarea  ff-code m-0 p-0 overflow-hidden fs-${fontSize}`} style={style} id="high-light">
                        {tokens.map((line, i) => {
                          return (<>
                            <div>
                              <pre className="m-0 p-0 overflow-hidden fs-6 lines">{i + 1}</pre>
                              <div {...getLineProps({ line, key: i })} className="ff-code li">
                                {line.map((token, key) => (
                                  <span className="ff-code" {...getTokenProps({ token, key })} />
                                ))}
                              </div>
                            </div>
                          </>)
                        })}
                      </pre>
                    )
                  }}
                </Highlight>
              </div>
            <code className="w-100 text-white ltr pb-5 p-2 fs-5 overflow-hidden result" id="result">
                {result.length > 0 ? result.map((item, index) => {
                  if (typeof item === "object") {
                    return <>
                      <pre
                        className="d-block ltr my-1 p-1"
                        key={index}>{`${JSON.stringify(item.yourResult)}(${typeof item.yourResult})  =>  `}{`${JSON.stringify(item.expectedResult)}(${typeof item.expectedResult})`}
                        {JSON.stringify(item.yourResult) === JSON.stringify(item.expectedResult) &&
                          typeof item.yourResult === typeof item.expectedResult ?
                          <span className="true fw-bold" key={index}>  true</span> : <span className="false fw-bold" key={index}>  false</span>}
                      </pre>
                    </>
                  } else {
                    return `${item}`;
                  }
                }) : "// your results will be here"}
              </code>
            </section>
          </div>
        </section>
      {result.length > 0 && result.every(obj => typeof obj === "object" && JSON.stringify(obj.yourResult) === JSON.stringify(obj.expectedResult) && typeof obj.yourResult === typeof obj.expectedResult ) ? <Success next={data.next} /> : null}
      </>
}

export { Code };