import React, {useState, useEffect, useRef} from "react";
import { Link } from "react-router-dom";
import logo from "./images/عرب expert dark.svg";
import free from "./images/free.png";
import { ReactComponent as Arabic } from "./images/arabic.svg";
import { Loader } from "./loader";
import "./styles/home.css";

const Ham = ({open}) => {
    return <>
        <div className={`ham ${open ? "open" : ""} px-5 py-4 d-md-none`}>
            <div className=" w-100 py-2">
                <Link to="/" className="text-decoration-none nav-links fs-5">الرئيسية</Link>
            </div>
            <div className=" w-100 py-2">
                <Link to="/challenge" className="text-decoration-none nav-links fs-5">التحديات</Link>
            </div>
            <div className=" w-100 py-2 pb-4">
                <Link to="/top" className="text-decoration-none nav-links fs-5">المتصدرون</Link>
            </div>
            <div className=" w-100 py-2">
                <Link to="/login" className="text-decoration-none nav-links fs-6 login">تسجيل الدخول</Link>
            </div>
            <div className=" w-100 py-2">
                <Link to="/signin" className="text-decoration-none signin col-lg-1 px-2 py-1 fs-6 fw-bold">إنشاء حساب</Link>
            </div>
        </div>
    </>
}

const Nav = () => {
    const [open, setOpen] = useState(false);
    return <>
        <nav className="container-fulid py-2">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-2 d-flex align-items-center">
                        <img src={logo} alt="logo" className="nav-logo" draggable="false"/>
                    </div>
                    <div className="col-lg-4 col-md-5 d-md-flex align-items-center justify-content-between d-none">
                        <Link to="/" className="text-decoration-none nav-links">الرئيسية</Link>
                        <Link to="/challenge" className="text-decoration-none nav-links">التحديات</Link>
                        <Link to="/top" className="text-decoration-none nav-links">المتصدرون</Link>
                    </div>
                    <span className="col-lg-2 d-lg-block d-none"></span>
                    <div className="col-lg-4 col-md-5 text-white ltr d-md-block d-none">
                        <Link to="/login" className="text-decoration-none nav-links col-lg-1 px-2 py-1 fs-6 login mx-1">تسجيل الدخول</Link>
                        <Link to="/signin" className="text-decoration-none signin col-lg-1 px-2 py-1 fs-6 fw-bold mx-1">إنشاء حساب</Link>
                    </div>
                    <div className="d-md-none d-block col-10 ltr">
                        <button className="p-2 ham-btn d-flex align-items-center justify-content-center" onClick={() => setOpen(!open)}>
                            <svg width="34" height="21" viewBox="0 0 49 36" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0 3H49" stroke="white" strokeWidth="5"></path><path d="M0 19H49" stroke="white" strokeWidth="5"></path><path d="M0 33H49" stroke="white" strokeWidth="5"></path></svg>
                        </button>
                    </div>
                </div>
            </div>
        </nav>
        <Ham open={open}/>
    </>
}

const Home = () => {
    const intro = useRef(null);
    useEffect(() => {
        intro.current.style.height = `${window.innerHeight}px`;
    }, []);
    return <>
        <Nav />
        <section className="container top" ref={intro}>
            <div className="row h-75 flex-wrap-reverse">
                <div className="col-md-6 col-12 h-100 d-flex align-items-center align-content-center flex-wrap">
                    <h1 className="text-white w-100 mb-2">ثاني منصة عربية لحل التحديات البرمجية بلغة <mark className="p-0 m-0">الجافاسكربت</mark></h1>
                    <a href="/challenge" className="px-3 py-1 fw-bold signin fs-5 text-decoration-none">إبدأ الآن</a>
                    <span className="c ff-code">const</span>
                    <span className="f ff-code">function</span>
                    <span className="i ff-code">isNaN</span>
                    <span className="fo ff-code">for</span>
                    <span className="w ff-code">while</span>
                    <span className="m ff-code">map</span>
                </div>
                <div className="col-md-6 col-12 h-100 d-flex align-items-center align-content-center big-logo justify-content-center">
                    <img src={logo} alt="logo"/>
                </div>
            </div>
            <div className="row justify-content-center text-white fs-3 h-25 align-items-center align-content-center">
                <span className="up-and-down text-white text-center fs-2 p-1">&#8595;</span>
            </div>
        </section>
        <section className="container">
            <div className="row free">
                <div className="col-md-6 d-flex align-items-center align-content-center justify-content-center">
                    <img src={free} alt="free"/>
                </div>
                <div className="col-md-6 d-flex align-items-center align-content-center justify-content-center">
                    <h2 className="text-white text-md-end text-center w-100">مجانية بالكامل مع كل الحلول البرمجية.</h2>
                </div>
            </div>
            <div className="row free flex-wrap-reverse">
                <div className="col-md-6 d-flex align-items-center align-content-center justify-content-center">
                    <h2 className="text-white text-md-end text-center w-100">المنصة بالكامل باللغة العربية.</h2>
                </div>
                <div className="col-md-6 d-flex align-items-center align-content-center justify-content-center">
                    <Arabic/>
                </div>
            </div>
        </section>
    </>
}

export { Home , Nav};